<template>
  <div class="create-users p-2">
    <div class="container">
      <h2 class="title">Create New User</h2>
      <div class="buttons">
        <router-link to="/CreateUsers" class="btn btn-primary m-3"
          >Create New Account</router-link
        >
        <router-link to="/" class="btn btn-success m-3">Logout</router-link>
      </div>
      <div class="user-card row">
        <form class="m-auto">
          <div class="form-row">
            <div class="col-md-6 mb-3">
              <input
                type="text"
                class="form-control"
                placeholder="Username"
                required="required"
                v-model="username"
              />
            </div>
            <div class="col-md-6 mb-3">
              <input
                type="password"
                class="form-control"
                placeholder="Password"
                required="required"
                v-model="password"
              />
            </div>
            <div class="col-md-6 mb-3">
              <input
                type="text"
                class="form-control"
                placeholder="First name"
                required="required"
                v-model="firstname"
              />
              <div class="valid-tooltip">Looks good!</div>
            </div>
            <div class="col-md-6 mb-3">
              <input
                type="text"
                class="form-control"
                placeholder="Last name"
                required="required"
                v-model="lastname"
              />
              <div class="valid-tooltip">Looks good!</div>
            </div>
          </div>
          <div class="form-row">
            <div class="col-md-6 mb-3">
              <input
                type="email"
                class="form-control"
                placeholder="Email Address"
                required="required"
                v-model="email"
              />
              <div class="invalid-tooltip">
                Please provide a valid Email Address.
              </div>
            </div>
            <div class="col-md-6 mb-3">
              <input
                type="text"
                class="form-control"
                placeholder="Phone Number"
                required="required"
                v-model="phonenumber"
              />
              <div class="invalid-tooltip">
                Please provide a valid Phone Number.
              </div>
            </div>
            <div class="col-12 mb-3">
              <select class="custom-select" required v-model="departmenta">
                <option selected disabled value="">Department...</option>
                <option v-for="depart in department" :key="depart.id">
                  {{ depart.Department }}
                </option>
              </select>
              <div class="invalid-tooltip">Please select a valid state.</div>
            </div>
          </div>
          <div class="buttons-bottom row">
            <div class="username col-12 col-md-6 mt-3 ml-md-3 text-md-left">
              <button class="btn btn-secondary" @click="addUser('username')">
                Add
              </button>
            </div>
            <div class="username col-12 col-md-5 mt-3 text-md-right">
              <router-link to="/index" class="btn btn-danger"
                >Cancel</router-link
              >
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import Department from "../json/department.json";
import Json from "../json/users.json";
export default {
  username: "",
  password: "",
  firstname: "",
  lastname: "",
  email: "",
  phonenumber: "",
  departmenta: "",
  name: "createUsers",
  data: function () {
    return {
      department: Department,
      users: Json,
    };
  },
  methods: {
    addUser() {
      if (
        (this.username &&
          this.password &&
          this.firstname &&
          this.lastname &&
          this.email &&
          this.phonenumber &&
          this.department) !== ""
      ) {
        this.Json.push(
          this.username,
          this.password,
          this.firstname,
          this.lastname,
          this.email,
          this.phonenumber,
          this.departmenta
        );
        this.username = "carma";
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.title {
  font-weight: 800;
}
.user-card {
  width: 80%;
  background-color: #bdc8d4;
  padding: 80px 30px;
  border-radius: 50px 0;
  margin: auto;
  button {
    width: 100px;
  }
  .username {
    margin-bottom: 15px;
    .label {
      margin-right: 5px;
      font-weight: 600;
    }
    .content {
      font-weight: 500;
      color: rgb(58, 58, 58);
    }
  }
}
</style>
