import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";
import UsersInfo from "../views/index.vue";
import CreateUsers from "../views/CreateUsers.vue";
import EditUsers from "../views/EditUsers.vue";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/index",
    name: "usersInfo",
    component: UsersInfo
  },
  {
    path: "/createUsers",
    name: "createUsers",
    component: CreateUsers
  },
  {
    path: "/editusers",
    name: "editUsers",
    component: EditUsers
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

export default router;
