<template>
  <div class="users-info">
    <div class="container">
      <h2 class="title">USERS INFO</h2>
      <div class="buttons">
        <router-link to="/CreateUsers" class="btn btn-primary m-3"
          >Create New Account</router-link
        >
        <router-link to="/" class="btn btn-success m-3">Logout</router-link>
      </div>
      <div v-if="users.length == 0" class="alert alert-danger">
        There Is No Users
      </div>
      <div
        class="user-card mb-3 row"
        v-for="(user, i) in users"
        :key="user.id + i"
        :username="user.username"
        :password="user.password"
        :firstname="user.firstname"
        :lastname="user.lastname"
        :email="user.email"
        :Phonenumber="user.Phonenumber"
        :Department="user.Department"
      >
        <div class="username col-md-6 ml-3 row">
          <span class="label">User ID: </span>
          <h5 class="content">{{ user.id }}</h5>
        </div>
        <div class="username col-md-6 ml-auto row">
          <span class="label">Username: </span>
          <h5 class="content">{{ user.username }}</h5>
        </div>
        <div class="username col-md-6 ml-3 row">
          <span class="label">Password: </span>
          <h5 class="content">{{ user.password }}</h5>
        </div>
        <div class="username col-md-6 ml-auto row">
          <span class="label">First Name: </span>
          <h5 class="content">{{ user.firstname }}</h5>
        </div>
        <div class="username col-md-6 ml-3 row">
          <span class="label">Last Name: </span>
          <h5 class="content">{{ user.lastname }}</h5>
        </div>
        <div class="username col-md-6 ml-auto row">
          <span class="label">Email Address: </span>
          <h5 class="content">{{ user.email }}</h5>
        </div>
        <div class="username col-md-6 ml-3 row">
          <span class="label">Phone Number: </span>
          <h5 class="content">{{ user.Phonenumber }}</h5>
        </div>
        <div class="username col-md-6 ml-auto row">
          <span class="label">Department: </span>
          <h5 class="content">{{ user.Department }}</h5>
        </div>
        <div class="username col-12 col-md-6 mt-3 ml-md-3 text-md-left">
          <router-link to="/editusers" class="btn btn-secondary"
            >Edit</router-link
          >
        </div>
        <div class="username col-12 col-md-5 mt-3 text-md-right">
          <button class="btn btn-danger" @click="deleteUser(i)">Delete</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Json from "../json/users.json";
export default {
  name: "usersInfo",
  data: function () {
    return {
      users: Json,
    };
  },
  methods: {
    deleteUser(i) {
      this.users.splice(i, 1);
    },
  },
};
</script>
<style lang="scss" scoped>
.title {
  font-weight: 800;
}
.user-card {
  width: 80%;
  background-color: #bdc8d4;
  padding: 80px 30px;
  border-radius: 50px 0;
  margin: auto;
  .btn,
  router-link {
    width: 100px;
  }
  .username {
    margin-bottom: 15px;
    .label {
      margin-right: 5px;
      font-weight: 600;
    }
    .content {
      font-weight: 500;
      color: rgb(58, 58, 58);
    }
  }
}
</style>
